<template>
    <div :class="'mypage-container '+mobileClass">

        <div class="contents">
            <div class="from-box">

                <div  class="sTitle"><span class="line">참가자 개인별 상세 내역 - {{subject}} 상세 내역</span></div>
                <div class="data-list-box">
                    <div class="data-list-item" v-for="(data,idx) in dataList">
                        <table class="data-list">
                            <thead>
                            <tr>
                                <th>문항</th>
                                <th>{{data.question}}</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <td>답변</td>
                                <td class="answer">
                                    {{data.answer}}
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div class="button-row">
                    <v-btn
                        type="button"
                        class="green"
                        @click="excelDown()">엑셀로 다운로드
                    </v-btn>
                    <v-btn
                        type="button"
                        class="submit-button"
                        @click="goList()">목록으로
                    </v-btn>
                </div>
            </div>
            <table id="excelTable" border="1" style="width:100%;">
                <thead>
                <tr>
                    <th colspan="3" style="height:50px"> 참가자 개인별  상세 내역 - {{subject}} 상세 내역 </th>
                </tr>
                </thead>
                <thead>
                <tr>
                    <th  style="height:40px;width:100px">번호</th>
                    <th  style="height:40px;width:200px">문항</th>
                    <th  style="height:40px;width:200px">답변</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item,idx) in dataList">
                    <td style="text-align:center;height:25px;">{{idx+1}}</td>
                    <td style="text-align:center">{{item.question}}</td>
                    <td style="text-align:center">{{item.answer}}</td>

                </tr>
                </tbody>
            </table>
        </div>

    </div>
</template>


<script>
	import JQ from 'jquery'
	import {mapGetters} from "vuex";
	import {
		API_URL_BROADCAST_LIVE_POLL_AFTER_WITH_USER_INFO
	} from "@/config/web/urls";
	import apiService from "@/config/web/apiService";

	export default {
		name: 'afterPollUserReport',
		data() {
			return {
				mobileView: false,
				mobileClass: '',
				subject: '',
			    totalCount: '',
				id:'',
                parentId:'',
                replyId:'',
				dataList: [],
		 	}
		},
		created() {
            if (this.$isMobile()) {
                this.mobileView = true;
                this.mobileClass = 'mobile'
            }
			this.replyId = this.$route.params.replyId;
            this.parentId = this.getGno;
			this.getData();
		},
        computed: {
            ...mapGetters('broadcast', ["getGno"]),

        },
		methods: {
			getData() {
				const frm = {replyId:this.replyId.toString(),parentId:this.parentId.toString()}
				apiService.post(API_URL_BROADCAST_LIVE_POLL_AFTER_WITH_USER_INFO, frm, (ret) => {
					this.subject = ret.responseInfo.name;
					let items= {};
					let keynum = '';
					let answer = {};
					let data = {};
					let answerList = [];
					let problems = JSON.parse(ret.info.problems);
					let answers = JSON.parse(ret.responseInfo.answers);
					for(let key in problems) {
						answer = answers[key];
						items = problems[key].items;
						data = {};
						data.question = problems[key].question;
						if (problems[key].prbType == 'subject') {
							data.answer = answer.singleAnswer;
						} else {
							if (problems[key].multigood == 'yes') {
								answerList = [];
								for (let ans of answer.multiAnswer) {
									keynum = parseInt(ans) - 1;
									answerList.push(ans + '(' + items[keynum].answer + ')');
								}
								data.answer = answerList.join(', ');

							} else {
								keynum = parseInt(answer.singleAnswer) - 1;
								data.answer = answer.singleAnswer + '(' + items[keynum].answer + ')';
							}
						}
						this.dataList.push(data);
					}

				});
			},
			goList() {
				this.$router.go(-1);
			},
			excelDown() {
				let tab_text = "<table border='1px'>";
				let exportTable = JQ('#excelTable').clone();
				tab_text = tab_text + exportTable.html();
				tab_text = tab_text + '</table>';

				 const fileName = '참가자 개인별 상세내역 - ' + this.subject + ' 상세내역.xlsx';
                const sheetName = '참가자 개인별 상세내역 - ' + this.subject + ' 상세내역';

                let elt = document.createElement("div");
                elt.innerHTML = tab_text;
                document.body.appendChild(elt);

                const wb = window.XLSX.utils.book_new();
                const workSheet = window.XLSX.utils.table_to_sheet(elt.getElementsByTagName("TABLE")[0]);
                document.body.removeChild(elt);


                // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
                window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);
                // step 4. 엑셀 파일 만들기
                const wbout = window.XLSX.write(wb, {bookType: 'xlsx', type: 'binary'});
                // step 5. 엑셀 파일 내보내기
                window.saveAs(new Blob([this.s2ab(wbout)], {type: "application/octet-stream"}), fileName);

            },
            s2ab(s) {
                const buf = new ArrayBuffer(s.length); //convert s to arrayBuffer
                const view = new Uint8Array(buf);  //create uint8array as viewer
                for (var i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF; //convert to octet
                return buf;
            },


		},

	}
</script>
<style lang="scss" scoped>
    ul {
        padding: 0;
        li {

        }
    }
    .mypage-container {
        .data-list-box {
            margin-top: 30px;
            background: #F4F4F4;
            border: solid 1px #DCDCDC;
            padding: 10px 40px 40px 40px;

            .data-list {
                width: 100%;
                color: #000;
                margin: 30px 0 10px 0;
                font-size: 11pt;

                th {
                    &:first-child {
                        width: 200px;
                        border-right: none;

                    }

                    text-align: center;
                    background: #E8F4FF;
                    border: solid 1px #DCDCDC;
                    height: 40px;
                    line-height: 40px;
                    font-weight: normal;
                }

                tbody tr {
                    td {
                        height: 50px;
                        background: #FFFFFF;
                        border: solid 1px #DCDCDC;
                        border-top: none;
                        text-align: center;

                        &:first-child {
                            border-right: none;
                        }

                        &.answer {
                            font-weight: bold;
                        }

                    }
                }

            }
        }

        .button-row {
            width: 630px;
            margin: 40px auto;

            button {
                width: 300px;
                border-radius: 30px;
                min-height: 60px;
                color: #fff;

                &.green {
                    margin-right: 30px;
                    background: #207245;
                    color: #fff;
                }

                &.submit-button {
                    background:#2D8CFF;
                }
            }
        }

        #excelTable {
            display: none;

            th, td {
                text-align: center;
            }
        }
        &.mobile {
            .data-list-box {
                padding:0 20px 20px 20px;
                .data-list {
                    th {
                        &:first-child {
                            width: 70px;

                        }
                    }
                }
            }
            .button-row {
                width: 100%;
                margin: 40px auto;
                display: flex;
                justify-content: space-between;

                button {
                    width: 48%;
                    border-radius: 25px;
                    min-height: 50px;
                    color: #fff;

                    &.green {
                        margin-right: 0;
                        background: #207245;
                        color: #fff;
                    }

                    &.submit-button {
                        background: #2D8CFF;
                    }
                }
            }
        }
    }

</style>
